.user {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 24px;
    position: relative;
    cursor: pointer;
    &__text {
        margin-left: 12px;
        text-align: left; }
    &__main {
        font-family: $helv;
        display: block;
        max-width: 160px;
        font-weight: 500;
        @include text-overflow; }
    &__secondary {
        max-width: 160px;
        font-family: $helv;
        color: $grey;
        @include text-overflow;
        .user_chat & {
            max-width: 260px; }
        .is-online & {
            color: $black; } }
    &__button {
        margin-left: auto; }
    &__time {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 13px;
        color: $grey; }
    &__active {
        position: absolute;
        bottom: 6px;
        right: 0;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background: $blue;
        font-size: 15px; }
    &_chat {
        &:after {
            content: '';
            position: absolute;
            top: -9px;
            left: -9px;
            bottom: -9px;
            right: -9px;
            z-index: -1;
            border-radius: $radius;
            transition: background-color $tr; }
        &:hover {
            &:after {
                background-color: $light-bg; } } } }

.user_chat.is-active {
    &:after {
        background-color: $blue; }
    .user__main,
    .user__secondary,
    .user__time {
        color: $white; } }




.avatar {
    display: block;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: $light-bg;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 110% 110%; }


.card-small {
    padding: 15px;
    border-radius: $radius;
    border: 1px solid $extra-light-grey;
    .user {
        margin-bottom: 0; } }

.add-members {
    &__row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 10px;
        width: 100%;
        justify-content: space-between; }
    &__input {
        flex-grow: 1;
        margin-left: 12px;
        font-weight: 500;
        font-size: 15px;
        font-family: $helv; }
    &__next {
        @include invis;
        .is-active & {
            @include vis;
            .btn-next {
                color: $blue;
                background-color: $light-blue; } } } }

.chat {
    width: 900px;
    padding: 0;
    position: relative;
    @include md {
        width: 700px; }
    @include sm {
        width: 100%; }
    &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 399px;
        width: 1px;
        background-color: $extra-light-grey;
        @include md {
            left: 299px; }
        @include sm {
            display: none; } }
    &__connections {
        float: left;
        width: 400px;
        padding: 20px;
        @include md {
            width: 300px; }
        @include sm {
            float: none;
            width: 100%; } }
    &__messages {
        float: left;
        width: 500px;
        padding: 20px;
        @include md {
            width: 400px; }
        @include sm {
            float: none;
            width: 100%;
            display: none; } }
    &__find {
        position: relative;
        margin-bottom: 40px; }
    &__top {
        position: relative;
        padding-bottom: 20px;
        &:after {
            position: absolute;
            content: '';
            left: -20px;
            right: -20px;
            bottom: 0;
            height: 1px;
            background-color: $extra-light-grey; }
        .user {
            margin-bottom: 0; } }
    &__main {
        padding-top: 100px;
        display: flex;
        flex-flow: column; }
    &__new-message {
        display: flex;
        align-items: {}
        margin-top: 30px; }
    &__input {
        width: auto;
        height: 35px;
        flex-grow: 1;
        margin: 0 15px; } }

.message-from,
.image-from {
    margin-left: auto;
    border-radius: 17px 17px 5px 17px;
    + .message-to {
        margin-top: 20px; } }

.message-from {
    max-width: 330px;
    padding: 10px;
    text-align: left;
    color: $white;
    background-color: $blue; }

.image-from {
    width: 230px;
    padding-bottom: 230px;
    @include cover; }




.message-to {
    position: relative;
    margin-right: auto;
    + .message-to {
        margin-top: 8px; }
    + .message-from,
    + .image-from {
        margin-top: 20px; }
    &__avatar {
        position: absolute;
        top: 50%;
        transform: translateY(-50%); }
    &__message {
        margin-left: 52px;
        background-color: $light-bg;
        padding: 10px;
        border-radius: 17px;
        line-height: 1.25; } }


